import React from 'react'
import { connect } from 'react-redux'
import styled from 'styled-components';
import { BREAKPOINT_LG,TOP_BAR_GRADIENT, HEADER_BACKGROUND,DARK_BORDER_COLOR,BREAKPOINT_MD, BREAKPOINT_SM} from '../components/types';
import Loader from '../components/common/loader'
import NavigationBar from '../components/common/navbar'
import HeroSection from '../components/common/heroSection'
import dynamic from 'next/dynamic';
import Footer from '../components/common/footer';
import Meta from '../components/common/meta';
const StyledHome = styled.div`

.home-top {
    
    background: ${HEADER_BACKGROUND};
    .home-top-bg{
    padding-top: 80px;   

  @media screen and (max-width: ${BREAKPOINT_LG}) {
      background-size: 75%;
   

        }
        @media screen and (max-width: ${BREAKPOINT_MD}) {
      background-size: 150%;
   

        }
}
}
`;
const IconsSet = dynamic(
  () => import('../components/icons/iconsSet'),
  { ssr: false,
    loading: () => <Loader/>}
);
class Index extends React.Component {
 
  componentDidMount () {

  }

  componentWillUnmount () {
  }

  render () {
    return (
        <div>
         <Meta />
          
         
        <NavigationBar page="index" />
        <StyledHome>
        <div className="home-top">
       <div className="home-top-bg">
        <HeroSection/></div></div></StyledHome>
        <IconsSet/>
        <Footer/>
        </div>)
  
}
}
export default Index;